@use "../tools" as *;

h1,
h2,
h3,
h4 {
	font-family: theme(font-family, serif);
	font-weight: 600;
	line-height: theme(line-height, tight);
}

h1 {
	font-size: theme(font-size, 4xl);
}

h2 {
	font-size: theme(font-size, 3xl);
}

h3 {
	font-size: theme(font-size, 2xl);
}

h4 {
	font-size: theme(font-size, xl);
}
